import React from "react";
import PriorityHighIcon from "@mui/icons-material/PriorityHigh";
import TipsAndUpdatesIcon from '@mui/icons-material/TipsAndUpdates';
import ErrorIcon from '@mui/icons-material/Error';
import "./note.css";
import { useNavigate } from "react-router-dom";
const Note = (props) => {

  const navigateTo=useNavigate()
  let styles = { noteDiv: {} };
  if (props.type === "empty") {
    styles = {
      noteDiv: {
        boxShadow: "inset 0px 0px 44px -18px rgba(231, 166, 26, 1)",
        textShadow: "4px 4px 13px rgba(231,166,26,0.6)",
        color: "white",
        border: "3px solid white",
      },
      outterdiv: {
        boxShadow: "0px 0px 44px -18px rgba(231, 166, 26, 1)",
      },
      Btn: {
        padding:"10px 20px",
        borderRadius:"10px",
        boxShadow: "0px 0px 44px -18px rgba(231, 166, 26, 1)",
        textShadow: "4px 4px 13px rgba(231,166,26,0.6)",
        color:"black",
        backgroundColor:" rgb(255, 203, 92)",
        cursor:"pointer"
       },
    };
  } 
  else if(props.type === "tip"){
    styles = {
        noteDiv: {
          boxShadow: "inset 0px 0px 44px -18px #33FF44",
          textShadow: "0px 0px 13px #33FF44",
          color: "white",
          border: "3px solid white",
        },
        outterdiv: {
          boxShadow: "0px 0px 44px -18px #33FF44",
        },
      };
  }
  else if(props.type === "error"){
    styles = {
        noteDiv: {
          boxShadow: "inset 0px 0px 44px -18px red",
          textShadow: "0px 0px 13px red",
          color: "white",
          border: "3px solid white",
        },
        outterdiv: {
          boxShadow: "0px 0px 44px -18px red",
        },
      };
  }
  
  else {
    styles = {
      noteDiv: {},
      outterdiv: {},
    };
  }


  const redirectToCreateDiv =()=>{
navigateTo("/user/tools/divisions")
  }

  return (
    <div className="outterdiv " style={styles.outterdiv}>
      <div className="noteDiv  gap10 padding_10_20" style={styles.noteDiv}>
        <div className="flex_center_center gap20 ">

       
        <div>
        
            {
                props.type==="tip"?(<TipsAndUpdatesIcon  id="muiIcon_tip"></TipsAndUpdatesIcon>):props.type==="empty"?( <PriorityHighIcon id="muiIcon_note"></PriorityHighIcon>):props.type==="error"?( <ErrorIcon id="muiIcon_error"></ErrorIcon>):null
            } 
          
        </div> 
        <div>
          {props.msg}
        </div>
        </div>
        <div className="flex_baselineEnd_center">
          {
            props.btnType==="Create Division"?(<button onClick={redirectToCreateDiv} style={styles.Btn}  >{props.btnType}</button>):null
          }
           
        </div>
      </div>
    </div>
  );
};

export default Note;
