import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";

//css in parent folder
const LectureDiv = (props) => {
  const { user, isAuthenticated } = useSelector((state) => state.user);

  const { userDetail, isUserDetailUpdated } = useSelector(
    (state) => state.userDetail
  );
  const { timeStamp, faculty, subject, type, presentStudents } =
    props.lectureData;
  const d = new Date(timeStamp);
  const [presenceChacker_state, set_presenceChacker_state] = useState(true);

  const styles = isAuthenticated
    ? user.settings.theme === "dark_theme"
      ? {
          main: {
            backgroundColor: "#190923",
            boxShadow: "1px 2px 29px -15px rgba(137, 43, 226, 0.644)",
            color:"white"
          },
        
        }
      : { main: {}}
    : { main: {} };

  function formatDate(input) {
    var datePart = input.match(/\d+/g),
      month = datePart[0].substring(0),
      day = datePart[1],
      year = datePart[2];

    return day + "/" + month + "/" + year;
  }

  //checking student is present or not

  const presenceChacker = () => {
    if (presentStudents.includes(userDetail._id)) {
      set_presenceChacker_state(true);
    } else {
      set_presenceChacker_state(false);
    }
  };

  useEffect(() => {
    presenceChacker();
  }, []);

  return (
    <div className="lecturesDiv boxShodow" style={styles.main}>
      <div>
        <span>{subject}</span>

        <span>
          {type} by {faculty}
        </span>

        <div>
          <span
            style={
              presenceChacker_state === false
                ? { backgroundColor: "#950000" }
                : null
            }
          >
            {presenceChacker_state ? "Present" : "Absent"}
          </span>
        </div>
      </div>
      <div>
        <span>
          Expired at, {formatDate(d.toLocaleDateString())},{" "}
          {d.toLocaleTimeString()}{" "}
        </span>
      </div>
    </div>
  );
};

export default LectureDiv;
