import axios from 'axios';
import React, { Fragment, useEffect } from 'react'
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import useSizing from '../../hooks/useSizing';
import "./ProctedRoute.css"

const ProtectedRoute = (props) => {
    let navigate = useNavigate();
    const windowSizing = useSizing();
    const {Comp ,styles}=props

  const { user, isAuthenticated } = useSelector((state) => state.user);

  useEffect(() => {
    if (!isAuthenticated) {
        navigate("/user/auth")
    }
  }, [ isAuthenticated])


  

  
  

  return (
    <Fragment> 
    {
        windowSizing.innerWidth >= 768 ?(<div  className='destokView'>
           <div className='sot absoluteCenter  '></div>
           
           
            <div className=' destok-galss'>


         <div className='absoluteCenter flex_center_center flex_column'>
            <span>DESTOK VERSION IS REDY BUT NOT OPTIMIZED.</span>
            <span>Please reduce the screen size to access the contex.</span>
         </div>
            </div>

        </div>): Comp
    }
    </Fragment>
   
  )
}

export default ProtectedRoute